.span-breed, .loader-old {
  font-weight: 900;
}

.error {
  color: red;
  font-weight: 900;
}

.loader {
  width: 80px;
  height: 80px;
  box-sizing: border-box;
  border: 8px solid #eac3b8;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  display: none;
}

